.search-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  & > h2 {
    margin: auto 0; } }

.search-icon {
  width: 24px;
  height: 24px; }

.open-search {
  height: 40px;
  justify-self: flex-end;
  width: 40px;
  border-radius: 50%;
  border: 1px solid  rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center; }

.search {
  height: 42px;
  border-radius: 15px;
  width: 100%;
  box-sizing: border-box;
  padding-left: 16px;
  outline: none;
  border: 1px solid  #6151C3; }

.close {
  width: 24px;
  height: 24px;
  opacity: 0.5;
  position: absolute;
  right: 16px;
  top: 8px; }

.search-bar {
  position: relative;
  width: 100%; }

.city-select {
  list-style: none;
  position: absolute;
  top: 45px;
  width: 100%;
  box-sizing: border-box;
  background-color: #2A2550;
  -webkit-box-shadow: 0px 5px 16px 2px rgba(0,0,0,0.68);
  box-shadow: 0px 5px 16px 2px rgba(0,0,0,0.68);
  border-radius: 15px;
  padding: 8px 0 0 16px;
  margin: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  & > li {
    padding: 0 0 8px; } }
